import React, { useState } from "react";
import { FiSearch } from "react-icons/fi";
import axios from "axios";
//import config from "../../config"; // API   
import './ImageGen.css'; //   CSS  

//const apiKey = config.OpenAiKey; // OpenAI API 
const apiKey = process.env.REACT_APP_GPT_KEY;

function ImageGen() {
    const [prompt, setPrompt] = useState(""); //   
    const [imageUrlList, setImageUrlList] = useState([]); //   URL 
    const [selectedImage, setSelectedImage] = useState(null); //   URL
    const [isLoading, setIsLoading] = useState(false); //  
    const [numImages, setNumImages] = useState(1); //   
    const [imageSize, setImageSize] = useState("512x512"); //   

    // GPT-4     
    const translatePrompt = async (prompt) => {
        try {
            const response = await axios.post(
                "https://api.openai.com/v1/chat/completions",
                {
                    model: "gpt-4", // GPT-4  
                    messages: [
                        { role: "system", content: "Translate the following prompt into English:" },
                        { role: "user", content: prompt }
                    ]
                },
                {
                    headers: {
                        "Content-Type": "application/json",
                        Authorization: `Bearer ${apiKey}`,
                    },
                }
            );

            const translation = response.data.choices[0].message.content.trim();
            return translation; //   
        } catch (error) {
            console.error("    :", error);
            return prompt; //      
        }
    };

    //   
    const generateImage = async () => {
        if (!prompt) {
            alert("    .");
            return;
        }

        setIsLoading(true);
        try {
            //   
            const translatedPrompt = await translatePrompt(prompt);

            const response = await axios.post(
                "https://api.openai.com/v1/images/generations",
                {
                    prompt: translatedPrompt, //   
                    n: numImages, //    
                    size: imageSize, //    
                },
                {
                    headers: {
                        "Content-Type": "application/json",
                        Authorization: `Bearer ${apiKey}`,
                    },
                }
            );

            const urls = response.data.data.map(item => item.url);
            setImageUrlList(urls);

        } catch (error) {
            console.error("    :", error.response ? error.response.data : error.message);
            setImageUrlList([]); //     
        } finally {
            setIsLoading(false); //   
        }
    };

    return (
        <div className="image-gen">
            <h2>  (DALLE)</h2>

            {/*  :     */}
            <div className="options-row">
                <div className="dropdown">
                    <label htmlFor="num-images"> </label>
                    <select
                        id="num-images"
                        value={numImages}
                        onChange={(e) => setNumImages(parseInt(e.target.value))}
                    >
                        {[1, 2, 3, 4, 5].map((num) => (
                            <option key={num} value={num}>
                                {num} 
                            </option>
                        ))}
                    </select>
                </div>

                <div className="dropdown">
                    <label htmlFor="image-size"> </label>
                    <select
                        id="image-size"
                        value={imageSize}
                        onChange={(e) => setImageSize(e.target.value)}
                    >
                        {["256x256", "512x512", "1024x1024"].map((size) => (
                            <option key={size} value={size}>
                                {size}
                            </option>
                        ))}
                    </select>
                </div>
            </div>

            {/*    */}
            <div className="wrap">
                <div className="search">
                    <input
                        type="text"
                        className="searchTerm"
                        placeholder="    "
                        value={prompt}
                        onChange={(e) => setPrompt(e.target.value)}
                    />
                    <button type="submit" className="searchButton" onClick={generateImage} disabled={isLoading}>
                        {isLoading ? " " : <FiSearch className="searchIcon" />}
                    </button>
                </div>
            </div>

            {/*    */}
            {imageUrlList.length > 0 && (
                <div className="image-preview">
                    {imageUrlList.map((url, index) => (
                        <div
                            key={index}
                            className={`image-container ${selectedImage === url ? "selected" : ""}`}
                        >
                            <span> {index + 1}</span>
                            <img
                                src={url}
                                alt={`Generated by DALLE ${index + 1}`}
                                onClick={() => setSelectedImage(url)} //  
                            />
                            <button onClick={() => setSelectedImage(url)}> </button>
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
}

export default ImageGen;
